@media only screen and (max-width: 767px) {
  #error-panel {
    position: absolute;
    background: #2c3338;
    width: 100%;
    min-height: 100%;
    display: flex;
    justify-content: center;
    margin-top: 7rem;
  }
  .error-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    width: 90vw;
    min-height: auto;
    background-color: rgba(10, 10, 10, 0.7);
    color: #8eadB1;
    border-radius: 1rem;
    box-shadow: 0 0 1rem black;
    padding: 2rem;
    margin-bottom: 2rem;
  }
  .error-wrapper > .error-heading {
    font-family: "Roboto Slab", serif;
    font-size: 5rem;
    color: #e4cd8a;
    margin: 2rem auto 1rem auto;
  }
  .error-wrapper > .error-subheading {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
  }
  .error-wrapper > .error-subheading > .error-paragraph {
    margin-top: 1rem;
  }
  .error-wrapper > .error-subheading > .error-paragraph > .error-color {
    color: #e4cd8a;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1279px) {
  #error-panel {
    position: absolute;
    background: #2c3338;
    width: 100%;
    height: 100%;
  }
  .error-wrapper {
    position: absolute;
    left: 50%;
    top: 55%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    width: 75vw;
    height: auto;
    background-color: rgba(10, 10, 10, 0.7);
    padding: 4rem 3rem;
    color: #8eadB1;
    border-radius: 1rem;
    box-shadow: 0 0 1rem black;
  }
  .error-wrapper > .error-heading {
    font-family: "Roboto Slab", serif;
    font-size: 5rem;
    color: #e4cd8a;
    margin-bottom: 2rem;
  }
  .error-wrapper > .error-subheading {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
  }
  .error-wrapper > .error-subheading > .error-paragraph > .error-color {
    color: #e4cd8a;
  }
}

